import avatarBlack from "./../../assets/images/black.png";
import { ADD_POST, DELETE_POST, SET_USER_PROFILE, SET_USER_STATUS } from './actionTypes';

const initialState = {
  profile: null,
  status: "",
  posts: [
    {
      postid: 1,
      userid: 1,
      message: "! - Post 1",
      username: "Stepawka",
      avatar: avatarBlack,
    },
    {
      postid: 2,
      userid: 1,
      message: "! - Post 2",
      username: "Stepawka",
      avatar: avatarBlack,
    },
  ],
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      return addPost(state, action.newPostText);
    }

    case DELETE_POST: {
      return deletePost(state, action.postid);
    }

    case SET_USER_PROFILE: {
      return setUserProfile(state, action.profile);
    }

    case SET_USER_STATUS: {
      return setUserStatus(state, action.status);
    }

    default:
      return state;
  }
};

const addPost = (state, newPostText) => {
  const post = {
    postid: state.posts.length + 1,
    userid: 1,
    message: newPostText,
    username: "Stepawka",
    avatar: avatarBlack,
  };

  return {
    ...state,
    posts: [...state.posts, post]
  };
};

const deletePost = (state, id) => ({
  ...state,
  posts: state.posts.filter(post => post.postid !== id)
});

const setUserProfile = (state, profile) => {
  return {
    ...state,
    profile,
  };
};

const setUserStatus = (state, status) => {
  return {
    ...state,
    status,
  };
};

export default profileReducer;
